<template>
  <div class="mt-4">
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary" @click="openModal('add')"></button>
    </div>
    <table class="table">
      <thead>
        <tr>
          <th width="200"></th>
          <th></th>
          <th width="80" class="text-right"></th>
          <th width="80" class="text-right"></th>
          <th width="100" class="text-center"></th>
          <th width="160"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td class="text-right">
            <span v-if="item.price">{{ item.price | currency }}</span>
            <span v-else></span>
          </td>
          <td class="text-center">
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group btn-group-sm" role="group">
              <button type="button" class="btn btn-outline-primary"
                    @click="openModal('modify', item)"></button>
              <button type="button" class="btn btn-outline-danger"
                    @click="openModal('del', item)"></button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination class="d-flex justify-content-center"
                :sent-page="pagination"
                @change-page="getProducts"></pagination>
    <!-- productModal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
          aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image" placeholder=""
                         v-model="tempProduct.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i v-if="status.fileUploading"
                    class="fas fa-spinner fa-spin"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files"
                        @change="uploadFile()">
                </div>
                <img class="img-fluid" alt=""
                    :src="tempProduct.imageUrl">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder=""
                        v-model="tempProduct.title">
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category" placeholder=""
                          v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit" placeholder=""
                          v-model="tempProduct.unit">
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price" placeholder=""
                          v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price" placeholder=""
                          v-model="tempProduct.price">
                  </div>
                </div>
                <hr>
                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control"
                            id="description" placeholder=""
                            v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control"
                            id="content" placeholder=""
                            v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                          v-model="tempProduct.is_enabled"
                          :true-value="1"
                          :false-value="0">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- delModal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
          aria-labelledby="delProductModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger delProductModalLabel-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';
import pagination from '../../Pagination';

export default {
  components: {
    pagination,
  },
  data() {
    return {
      products: [],
      tempProduct: {},
      pagination: {},
      productAction: '', //  product  add/modify/del
      isLoading: false, // full page loading
      status: {
        fileUploading: false, // part loading
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/products?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(action, item) {
      const vm = this;
      vm.productAction = action;
      vm.isLoading = true;
      switch (action) {
        case 'add':
          vm.tempProduct = {};
          $('#productModal').modal('show');
          break;
        case 'modify':
          vm.tempProduct = Object.assign({}, item);
          $('#productModal').modal('show');
          break;
        case 'del':
          vm.tempProduct = Object.assign({}, item);
          $('#delProductModal').modal('show');
          break;
        default:
          vm.tempProduct = {};
      }
      vm.isLoading = false;
    },
    updateProduct() {
      const vm = this;
      vm.isLoading = true;
      // 
      let api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/product`;
      let httpMethod = 'post';
      // 
      if (vm.productAction === 'modify') {
        api += `/${vm.tempProduct.id}`;
        httpMethod = 'put';
      // 
      } else if (vm.productAction === 'del') {
        api += `/${vm.tempProduct.id}`;
        httpMethod = 'delete';
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {
        if (response.data.success) {
          vm.getProducts(vm.pagination.current_page);
          // Update Success
          this.$bus.$emit('message:push', response.data.message, 'success');
        } else {
          // Update Failure
          this.$bus.$emit('message:push', response.data.message, 'danger');
        }
        vm.isLoading = false;
        $('#productModal').modal('hide');
        $('#delProductModal').modal('hide');
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const id = this.$refs.files.id;
      const vm = this;
      const formData = new FormData();
      const url = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/upload`;
      vm.status.fileUploading = true;
      formData.append('file-to-upload', uploadedFile);
      this.$http.post(url, formData, {
        header: 'multipart/form-data',
      }).then((response) => {
        if (response.data.success) {
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
          document.getElementById(id).value = '';
        } else {
          this.$bus.$emit('message:push', response.data.message, 'danger');
        }
        vm.status.fileUploading = false;
      });
    },
  },
  created() {
    this.getProducts();
  },
};
</script>



